@import url(https://fonts.googleapis.com/css2?family=Sulphur+Point:wght@300;400;700&display=swap);
body, html {
  padding: 0;
  margin: 0;
  --color-primary: #00bcd4;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Sulphur Point', sans-serif;

}

*::selection {
  background: #f76a8c;
  color: #fff;
}


* {
    box-sizing: border-box;
    overflow-x: hidden;
    /* list-style: none; */
    margin: 0 ;
    padding: 0;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    min-height: 100vh;
    width: 100vw;
    align-items: center;
}

.file__select__container, .file__select__lable,  .preview {
    display: flex;
    justify-content: center;
}

.instruction__msg {
    width: 60vw;
    padding: 0 0.5rem;
}

.instruction__msg ol li {
    margin-left: 1em;
    overflow: visible;
}

.instruction__msg h3 {
    font-size: 1.2rem;
    font-weight: 700;
}
  
.file__select__container {
    border: 3px dashed var(--color-primary);
    height: 300px;
    width: 60vw;
    align-items: flex-end;
    padding: 20px;
}

.drop__message {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.upload__icon {
    height: 70px;
    width: 70px;
}
  
.upload__icon g {
    fill: var(--color-primary);
}

.browse__file__message {
    font-size: 1.5rem;
    margin: 1rem;
}

.select__btn {
    height: 3rem;
    width: 13rem;
    background: var(--color-primary);
    color: #fff;
    font-size: 1.5rem;
    border: none;
    cursor: pointer;
}

  
.preview {
    margin-bottom: 1.5rem;
}

.img__container {
    height: 179px;
    width: 107px;
    text-align: center;
    margin: 5px;
    padding: 5px 0 0 0;
    overflow: hidden;
    border: 1px solid var(--color-primary);
}

.selected__image {
    height: 120px;
    width: calc(100% - 6px);
    margin: 0 3px;
    object-fit: contain;
    
}

.img__name {
    display: block;
    background: var(--color-primary);
    color: #fff; 
    width: 107px;
    padding: 0 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

h6 {
    margin: 0;
    font-size: 0.67em;
    line-height: 1.7;
}

.img__size {
    color: var(--color-primary);
}

.upload__btn {
    width: 60vw;
    height: 2.5rem;
    background: #3797a4;
    color: #fff;
    font-weight: 400;
    font-size: 1.2rem;
    margin: 2rem 0;
    border: #3797a4;
    cursor: pointer;
}


@media screen and (max-width: 53em) {
    .file__select__container, .upload__btn, .instruction__msg {
        width: 90vw;
    }

    .browse__file__message {
        width: 13rem;
        font-size: 1.2rem;
    }

    .select__btn {
        width: 15rem;
    }

    .instruction__msg li{
        font-size: 0.8rem;
    }

}

