* {
    text-decoration: none !important;
}

.nav {
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5rem;
    border-bottom: 1px solid #e0e0e0;
}

.logo li{
    font-size: 2rem;
    font-weight: 700;
    color: var(--color-primary);
}

.nav__items li{
    font-size: 1.4rem;
    font-weight: 400;
    color: var(--color-primary);

}

@media screen and  (max-width: 53em) {

    .nav {
        padding: 0 1rem;
        height: 3rem;
    }

    .logo li {
        font-size: 1.3rem;
    }

    .nav__items li {
        font-size: 1rem;
    }
}

.crop__container {
    display: flex;
    width: 100vw;
    justify-content: space-around;
    padding:2rem;
}

.crop__img__container {

    width: 60vw;
    height: 80vh;
    border: 1px solid var(--color-primary);
    overflow: hidden;
    object-fit: contain;
}


.preview__and__btn {
    display: flex;
    width: 400px;
    height: 600px;
    flex-direction: column;
    align-items: center;
    margin-left: 1rem;
}

.img__preview {
    height: 300px;
    width: 100%;
    border: 1px solid var(--color-primary);
    overflow: hidden;

}

.img__details {
    text-align: center;
}

.img__details h6 {
    margin: 0.5rem 0;
    font-size: 0.7rem;
}

.crop__and__submit {

    height: 4rem;
    width: 100%;
    border-radius: 5px;
    border: 1px solid var(--color-primary);
    background: var(--color-primary);
    color: #000000;
    font-size: 1.3rem;
    font-weight: 400;
    margin-top: 2rem;
}


.upload__all {
    width: calc(100vw - 4rem);
    margin: 2rem 2rem 5rem 2rem;
    height: 4rem;
    border-radius: 5px;
    border: 1px solid #3797a4;
    background: #3797a4;
    color: #ffffff;
    font-size: 1.3rem;
    font-weight: 400;
}


@media screen and (max-width:53em) {

    .crop__container {
        flex-direction: column;
        align-items: center;
    }   

    .crop__container:not(:last-child) {
        border-bottom: 1px solid #000000;
    }

    .crop__img__container {
        width: 90vw;
        height: 50vh;
        margin-bottom: 2rem;
    }

    .preview__and__btn {
        width: 90vw;
        height: 50vh;
        margin-left: 0;
    }

    .upload__all {
        width: 90vw;
        margin: 2rem 1rem 3rem 1rem;
        height: 3.5rem;
    }
}
.album__container {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    margin-top: 2rem;

}

.album__container h2 {
    margin-bottom: 1rem;
    text-decoration: underline;
}


.album__card {
    width: calc(70% - 4rem);
    height: 250px;
    border: 1px solid #000000;
    border-radius: 5px;
    padding: 1rem;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 2rem 0;
}


.album__card img {
    width: 100%;
    height: 100%;
    object-fit: 100% 100%;
    padding: 0 2px;
    cursor: pointer;
}

.cropped_img1, .cropped_img2, .cropped_img3, .cropped_img4 {
    height: calc(250px - 2rem);
}

.cropped_img1 {
    flex: 33% 1;
}

.cropped_img2 {
    flex: 15% 1;
}

.cropped_img3 {
    flex: 30% 1;
}

.cropped_img4 {
    flex: 22% 1;
}

.loading__message  {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    height: 60vh;
}

@media screen and (max-width: 53em) {
    .album__container {
        padding: 1rem;
        margin-top: 1rem;
    }

    .album__card {
        width: 100%;
        height: 200px;
        padding: 0.5rem;
    }

    .album__container h2 {
        margin-bottom: 0;
    }

    .cropped_img1, .cropped_img2, .cropped_img3, .cropped_img4 {
        height: calc(200px - 1rem);
    }

    .album__card img {
        padding: 0 0.8px;
    }

    .loading__message  {
        font-size: 1rem;
        height: 80vh;
    }
}
